import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { loadFont } from './utils/font';
import { getInterpolatedColours, getClosestColour, color, getPointFromAngleRadius, colorToString } from './utils/utils';
import Vector from './utils/vector';

import GothamFont from '../data/Gotham-Bold.ttf';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const colourArr = getInterpolatedColours([
  color(241, 100, 92),
  color(148, 147, 242),
  color(93, 213, 247),
  color(241, 100, 92),
], 250);

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

let globalAngle = 0;

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  globalAngle += 0.1;

  if (path) {
    ctx.save();
    ctx.translate(
      ((width - path.boundingBox.width) / 2),
      (height / 2) + (path.boundingBox.height / 2),
    );

    ctx.fillStyle = 'red';

    for (let i = 0; i < path.points.length; i++) {
      for (let p = 0; p < path.points[i].length; p++) {
        const { x, y, angle } = path.points[i][p];

        const { x: x1, y: y1 } = getPointFromAngleRadius(globalAngle, 25, x, y);

        ctx.beginPath();
        ctx.strokeStyle = colorToString(getClosestColour(((angle) % (Math.PI * 2)) / (Math.PI * 2), colourArr));
        ctx.moveTo(x, y);
        ctx.lineTo(x1, y1);
        ctx.stroke();
      }
    }

    for (let i = 0; i < path.points.length; i++) {
      for (let p = 0; p < path.points[i].length; p++) {
        const { x, y } = path.points[i][p];

        if (p == 0) {
          ctx.moveTo(x, y);
        } else {
          ctx.lineTo(x, y);
        }
      }
      ctx.closePath();
    }

    ctx.fillStyle = 'white';
    ctx.fill();
    ctx.restore();
  }
});

const angle = (cx, cy, ex, ey) => {
  let dy = ey - cy;
  let dx = ex - cx;
  let theta = Math.atan2(dy, dx); // range (-PI, PI]
  if (theta < 0) theta = (Math.PI * 2) + theta; // range [0, 360)
  return theta;
}

loadFont(GothamFont)
  .then(Font => {
    let path = Font.getOptimisedPath('HELLO', 100, { fidelity: 2 });

    path.points = path.points.map(shape => {
      return shape.map((p, index) => {
        if (index == 0) {
          return {
            ...p,
            angle: 0
          }
        } else {
          return {
            ...p,
            angle: angle(
              shape[index - 1].x,
              shape[index - 1].y,
              p.x,
              p.y
            )
          };
        }
      });
    });

    window.path = path;

    app.start();
  });
