import * as opentype from 'opentype.js';
import { linePath, quadraticBezierPath, cubicBezierPath, getPointOnLinePath } from './paths';
import { distance } from './utils';

export class Font {
  constructor(fontPath) {
    this.fontPath = fontPath;
  }

  getPath(str, size, x = 0, y = 0) {
    return this.fontPath.getPath(str, x, y, size);
  }

  getOptimisedPath(str, size, { fidelity = 10, x = 0, y = 0 } = {}) {
    const path = this.fontPath.getPath(str, x, y, size);

    let points = [[]];
    let lastPoint = {};

    const getFidelity = ({ x: x1, y: y1 }, { x: x2, y: y2 }, fidelity) => {
      return Math.round(distance(x1, y1, x2, y2) / fidelity);
    }

    for (let i = 0; i < path.commands.length; i += 1) {
        const cmd = path.commands[i];

        let test;

        if (cmd.x === lastPoint.x && cmd.y === lastPoint.y) {
          continue;
        }

        if (cmd.type === 'L') {
          points[points.length - 1] = [
            ...points[points.length - 1],
            ...linePath(
              lastPoint,
              {
                x: cmd.x,
                y: cmd.y,
              },
              getFidelity(lastPoint, cmd, fidelity)
            )
          ];
        } else if (cmd.type === 'C') {
          points[points.length - 1] = [
            ...points[points.length - 1],
            ...cubicBezierPath(
              lastPoint,
              {
                x: cmd.x1,
                y: cmd.y1,
              },
              {
                x: cmd.x2,
                y: cmd.y2,
              },
              {
                x: cmd.x,
                y: cmd.y,
              },
              getFidelity(lastPoint, cmd, fidelity)
            )
          ];
        } else if (cmd.type === 'Q') {
          points[points.length - 1] = [
            ...points[points.length - 1],
            ...quadraticBezierPath(
              lastPoint,
              {
                x: cmd.x1,
                y: cmd.y1,
              },
              {
                x: cmd.x,
                y: cmd.y,
              },
              getFidelity(lastPoint, cmd, fidelity)
            )
          ];
        } else if (cmd.type === 'Z' && i < path.commands.length - 1) {
          points.push([]);
        }

        lastPoint = {
          x: cmd.x,
          y: cmd.y
        };
    }

    const boundingBox = path.getBoundingBox();
    boundingBox.width = Math.abs(boundingBox.x2 - boundingBox.x1);
    boundingBox.height = Math.abs(boundingBox.y2 - boundingBox.y1);

    return {
      boundingBox,
      points: points.map(this._removeDuplicates)
    };
  }

  _removeDuplicates(shape) {
    return shape.filter((p, index) => {
      if (index == 0) {
        return true;
      }

      return (p.x !== shape[index - 1].x || p.y !== shape[index - 1].y);
    })
  }
}

export function loadFont(fontPath) {
  return new Promise((resolve, reject) => {
    opentype.load(fontPath, (err, font) => {
      resolve(new Font(font));
    });
  });
}
