export default class Vector {
  constructor({ x = 0, y = 0, magnitude = 1 } = {}) {
    this.x = x;
    this.y = y;
    this.magnitude = magnitude;

    return this;
  }


  /**
   * Sets the x and y position at the same time
   *
   * @param {Number} x
   * @param {Number} y
   */
  set(x, y) {
    this.x = x;
    this.y = y;

    return this;
  }


  /**
   * Sets the magnitude of the vector.
   * This is like how 'far' the vector moves
   * each frame
   *
   * @param {Number} magnitude
   */
  setMag(magnitude) {
    this.normalize();
    this.magnitude = magnitude;

    return this.mult(this.magnitude);
  }


  /**
   * Sets the angle of the vector.
   * This works out the x/y combo to
   * move in a certain direction
   *
   * @param {Number} angle in radians
   */
  setAngle(angle) {
    this.x = Math.cos(angle);
    this.y = Math.sin(angle);

    return this.mult(this.magnitude);
  }


  /**
   * Gets the current angle of the vector
   */
  getAngle() {
    return Math.atan2(this.y, this.x);
  }


  /**
   * Gets the angle of a vector like object
   * from this vector. Used on velocity vectors
   *
   * @param {object} Vector
   */
  getAngleOf({ x, y, magnitude }) {
    return Math.acos(this.dot({ x, y }) / (this.magnitude * magnitude));
  }


  /**
   * Gets the angle of a vector like object
   * from this vector
   *
   * @param {object} Vector
   */
  getAngleInSpaceOf({ x, y }) {
    return Math.atan2(x - this.x, y - this.y);
  }


  /**
   * Rotates the vector by an angle
   *
   * @param {Number} angle in radians
   */
  rotate(angle) {
    const _angle = this.getAngle() + angle;
    return this.setAngle(_angle % (Math.PI * 2));
  }


  /**
   * Returns the vector to a unit of 1
   */
  normalize() {
    return this.mult(1 / this.magnitude);
  }


  /**
   * Multiplies the vector uniformly
   * by a number
   *
   * @param {Number} num
   */
  mult(num) {
    this.x *= num;
    this.y *= num;

    return this;
  }


  /**
   * Divides the vector uniformly
   * by a number
   *
   * @param {Number} num
   */
  div(num) {
    this.x /= num;
    this.y /= num;

    return this;
  }


  /**
   * Subtracts a vector like object
   *
   * @param {object} Vector
   */
  sub({ x, y }) {
    this.x -= x;
    this.y -= y;

    return this;
  }


  /**
   * Adds a vector like object
   *
   * @param {object} Vector
   */
  add({ x, y }) {
    this.x += x;
    this.y += y;

    return this;
  }


  /**
   * Returns dot product of 2 vectors
   *
   * @param {object} Vector
   */
  dot({ x, y }) {
    return this.x * x + this.y * y;
  }


  /**
   * Gets distance between 2 vectors
   *
   * @param {object} Vector
   */
  getDistance({ x, y }) {
    let a = this.x - x;
    let b = this.y - y;
    return Math.sqrt(a * a + b * b);
  }


  /**
   * Sets a random angle for the vector
   */
  randomAngle() {
    return this.setAngle((Math.PI * 2) * Math.random());
  }

  /**
   * Returns a new vector from current
   * settings
   */
  clone() {
    return new Vector({
      x: this.x,
      y: this.y,
      magnitude: this.magnitude
    });
  }
}
